import localQuotes from './quotes.js';

const state = { quotes: [] };

const API_URL = 'https://type.fit/api/quotes';
const TWITTER_URL = 'https://twitter.com/intent/tweet';
const LONG_QUOTE_LIMIT = 120;

const quoteContainer = document.getElementById('quote-container');
const quoteText = document.getElementById('quote');
const authorText = document.getElementById('author');
const twitterBtn = document.getElementById('twitter');
const newQuoteBtn = document.getElementById('new-quote');
const loader = document.getElementById('loader');

const toggleLoader = () => {
    loader.hidden ^= 1;
    quoteContainer.hidden ^= 1;
};

const showRandomQuote = () => {
    toggleLoader();

    const randomInt = Math.floor(Math.random() * state.quotes.length);
    const quote = state.quotes[randomInt];

    if (quote.text.length > LONG_QUOTE_LIMIT)
        quoteText.classList.add('long-quote');
    else quoteText.classList.remove('long-quote');

    quoteText.textContent = quote.text;
    authorText.textContent = quote.author ?? 'Unknown';

    toggleLoader();
};

const fetchQuotes = async () => {
    try {
        toggleLoader();
        const response = await fetch(API_URL);
        const quotes = await response.json();
        state.quotes = quotes;
    } catch (err) {
        state.quotes = localQuotes;
    } finally {
        toggleLoader();
    }
};

const addHandlerTwitterBtn = () => {
    twitterBtn.addEventListener('click', () => {
        window.open(
            `${TWITTER_URL}?text=${quoteText.textContent} - ${authorText.textContent}`,
            '_blank'
        );
    });
};

const addHandlerNewQuoteBtn = () => {
    newQuoteBtn.addEventListener('click', showRandomQuote);
};

(async () => {
    loader.hidden = true;
    quoteContainer.hidden = false;
    await fetchQuotes();
    showRandomQuote();
    addHandlerTwitterBtn();
    addHandlerNewQuoteBtn();
})();
